import React from 'react';
import { Link } from 'react-scroll';

const Footer = () => {
    return(
         <footer className="site-footer">
      <div className="container">
        <div className="row">
          <div className="col-lg-3">
            <h2 className="footer-heading mb-3">About Me</h2>
                <p>Self-taught Frontend developer from St. Petersburg</p>
          </div>
          <div className="col-lg-8 ml-auto">
            <div className="row">
              <div className="col-lg-6 ml-auto">
                <h2 className="footer-heading mb-4">Sections:</h2>
                <ul className="list-unstyled">
                <li className="nav-item ">
                  <Link activeClass="active" className="nav-link " to="aboutme" spy={true} smooth={true} duration={500} >About Me</Link>
                </li>
                <li className="nav-item ">
                  <Link activeClass="active" className="nav-link " to="portfolio" spy={true} smooth={true} duration={500} >Portfolio</Link>
                </li>
                <li className="nav-item ">
                  <Link activeClass="active" className="nav-link " to="footer" spy={true} smooth={true} duration={500} >Contacts</Link>
                </li>
                </ul>
              </div>
              <div className="col-lg-6">
                <h2 className="footer-heading mb-4">Contacts</h2>
                <div className="social_29128 white mb-5">
                <ul className="list-unstyled">
                  <li><a href="https://github.com/alexkozopolianski?tab=repositories">Github</a></li>
                  <li><a href="https://www.linkedin.com/in/alex-kozopolianskii-461308205/">LinkedIn</a></li>
                </ul>
                 </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
    )
}

export default Footer;