import React from 'react';
import { Link} from 'react-scroll';

const Navbar = () => {
    return(
        
        <nav id="navbar-example2" className="navbar navbar-light">
            <Link activeClass="active" 
            className="nav-link text-white" 
            to="header" spy={true} smooth={true} duration={500}>
                Alex Kozopolianski
            </Link>
        <ul className="nav nav-pills text-black">
          <li className="nav-item ">
          <Link activeClass="active" className="nav-link text-white" to="aboutme" spy={true} smooth={true} duration={500} >About Me</Link>
          </li>
          <li className="nav-item ">
          <Link activeClass="active" className="nav-link text-white" to="portfolio" spy={true} smooth={true} duration={500} >Portfolio</Link>
          </li>
          <li className="nav-item ">
          <Link activeClass="active" className="nav-link text-white" to="footer" spy={true} smooth={true} duration={500} >Contacts</Link>
          </li>
         
        </ul>
      </nav>
    )
}

export default Navbar;